//fetch.js
import errorCode from "@/utils/errorCode";
const BaseUrl = 'http://localhost:8088'  //url
export default ({ //
	url,
	method,
	data,
}) => {
	// 
	const token = uni.getStorageSync('token');
	let header={};
	if(token!=null&&token!='')
	{  // header
		header={token:token}
	}
	// uni.showLoading({
	// 	title: ""
	// });

	//
	return new Promise(function(resolve, reject) {
	
		uni.request({
			url: BaseUrl + url,  // 
			method: method,   
			timeout: 60000, //
			dataType: "json", 
			responseType: 'text',
			data: data,  //
			withCredentials:true,
			header: header,
			// 
			success(res) {//
				console.log(res)
				// 
				const code = res.data.code || 200;
				// 
				const msg = errorCode[code] || res.data.msg || errorCode['default'];
				// 
				switch (code) {
				    //token
				    case 401:
					uni.showToast({
									title: "",
									icon:'exception',
									duration:850
								});
				        //token,
				        // window.sessionStorage.clear()
						uni.clearStorageSync("token")
						uni.clearStorageSync("user")
				        Vue.prototype.$router.push("/login")
				        break;
				    //
				    case 500:
				       uni.showToast({
				       				title: msg,
				       				icon:'exception',
				       				duration:850
				       			});
				        break;
				    case 200:
						resolve(res.data);
						break;
				        // return res.data;
				    //
				    default:
				       uni.showToast({
				       				title: msg,
				       				icon:'exception',
				       				duration:850
				       			});
				}
				//
				reject(res)
				// Promise 
				// return Promise.reject(new Error(msg))
				// if (res.data.code == 0) {
				// 	resolve(res.data.data)
				// }
			},
			fail(err) {
				reject(err)
			}
		})
	})
}